<template>
    <div class="profile_page">
        <div class="profile_head">
            <div class="head_left">
                <div><span class="img">头像</span><span class="user_name">张三</span></div>
            </div>
            <div class="head_right">
                <p class="time">总课时：<span class="num">6</span></p>
                <p class="credit">总学分：<span class="num">18</span></p>
            </div>
        </div>
        <div class="profile_main">
            <div class="main_list" @click="toCourse()">
                <div class="main_list_left">
                    <p class="list_top">
                        <i class="iconfont icon-kecheng"></i>
                        <span class="list_title">在线课程</span>
                    </p>
                    <p class="list_bottom">
                        <span class="time">课时：<i class="num">3</i></span>
                        <span class="credit">学分：<i class="num">6</i></span>
                    </p>
                </div>
                <div class="main_list_icon">
                    <i class="iconfont icon-jiantouyou"></i>
                </div>
            </div>
            <div class="main_list" @click="toTrain()">
                <div class="main_list_left">
                    <p class="list_top">
                        <i class="iconfont icon-peixunguanli"></i>
                        <span class="list_title">培训班级</span>
                    </p>
                    <p class="list_bottom">
                        <span class="time">课时：<i class="num">3</i></span>
                        <span class="credit">学分：<i class="num">6</i></span>
                    </p>
                </div>
                <div class="main_list_icon">
                    <i class="iconfont icon-jiantouyou"></i>
                </div>
            </div>
            <div class="main_list" @click="toExam()">
                <div class="main_list_left">
                    <p class="list_top">
                        <i class="iconfont icon-exam"></i>
                        <span class="list_title">考试</span>
                    </p>
                    <p class="list_bottom">
                        <span class="time">课时：<i class="num">--/--</i></span>
                        <span class="credit">学分：<i class="num">6</i></span>
                    </p>
                </div>
                <div class="main_list_icon">
                    <i class="iconfont icon-jiantouyou"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    methods:{
        toExam(){
            this.$router.push(`/profile/exam/`);
        },
        toTrain(){
            this.$router.push(`/profile/train/`);
        },
        toCourse(){
            this.$router.push(`/profile/course/`);
        }
    }
}
</script>

<style lang="sass">

</style>





